
import { defineComponent } from "vue";

import VForm from "@/components/form/VForm.vue";
import FormField from "@/components/fields/FormField/FormField.vue";
import InputField from "@/components/fields/InputField/InputField.vue";
import CheckboxField from "@/components/fields/CheckboxField/CheckboxField.vue";
import TextareaField from "@/components/fields/TextareaField/TextareaField.vue";
import PayloadField from "@/components/terminals/fields/PayloadTemplateField.vue";
import VButton from "@/components/buttons/BaseButton/BaseButton.vue";
import { TerminalsActions } from "@/store/modules/terminals";

import SettingsSelect from "./fields/SettingsSelect.vue";
import RequestTypeSelect from "./fields/RequestMethodSelect.vue";
import SynctypeSelect from "./fields/SyncTypeSelect.vue";
import { useToast } from "vue-toastification";

const createInitialData = (id: string) => ({
  id,
  name: "Name",
  settings: "",
  max_offline_sum: 0,
  is_default_offline: false,
  is_for_all_card: true,
  can_user_add_card: false,
  endpoint_result: "",
  add_get: "",
  headers: "",
  payload: "",
  sign_stract: "",
  card_wait: 30,
  req_type: "",
  need_shift: false,
  sync_type: "",
  sync_period: 30,
  update_all_term: false,
});

export default defineComponent({
  inject: ["rules"],
  props: {
    actionsFixed: {
      type: Boolean,
      required: false,
      default: false,
    },
  },

  components: {
    FormField,
    InputField,
    CheckboxField,
    TextareaField,
    RequestTypeSelect,
    PayloadField,
    SettingsSelect,
    VButton,
    SynctypeSelect,
    VForm,
  },

  setup() {
    const toast = useToast();

    return { toast };
  },

  data() {
    return {
      initialValues: createInitialData(""),
    };
  },

  mounted() {
    const id = this.$route.query.id as string;
    this.initialValues = createInitialData(id || "");
  },

  computed: {
    loading(): boolean {
      return this.$store.state.terminals.formLoading;
    },
  },

  methods: {
    sendConfig() {
      this.$store.dispatch(
        TerminalsActions.CREATE_OFFLINE_TERMINAL,
        (this.$props as any).configData
      );
    },
  },
});
